<template>
  <div class="test">
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />

    <van-row gutter="20">
      <van-col span="24" class="subtitle">
        <!-- <div class="subname"></div> -->
        <div class="subname"></div>
        <div class="cnt">
          <!-- 8
          <span></span>  -->
          
        </div>
      </van-col>

      <!-- <van-col span="24">
        <img class="home-bg" src="@/assets/imgs/home.jpeg" alt="" @click="handleImgClick">
      </van-col> -->
      <van-col span="24">
        <van-cell-group>
          <van-radio-group v-model="form.category" class="radio">
            <van-radio name="1" style="padding:0 10px;"></van-radio>
            <van-radio name="2" style="padding:0 10px;"></van-radio>
          </van-radio-group>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.id" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.province" placeholder="" label="" disabled/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.score" placeholder="" label="" type="number" maxlength="5" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <div class="times-tip">{{times}}</div>
      </van-col>
      <van-col span="24">
        <van-button type="primary" bottom-action class="btn" @click="handleConfirm"></van-button>
      </van-col>

    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {
        province: '',
        category: '1'
      },
      times: 10
    }
  },
  methods: {
    handleImgClick() {
      // 
      this.$router.push({ name: 'OpenAccount' })
    },
    handleConfirm() {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          this.$router.push({ name: 'Answer' })
        })
        .catch(() => {
          this.$toast.fail('')
        })
    }
  },
  created() {}
}
</script>

<style lang="scss" scoped>
.home-bg {
  width: 100%;
  margin-bottom: 20px;
}
.test {
  margin-bottom: 10px;
  .btn {
    margin-top: 20px;
  }

  .radio {
    display: flex;
    padding: 10px;
    text-align: center;
    justify-content: space-evenly;
  }
  .subtitle {
    font-size: 20px;
    color: #cc0000;
    padding-top: 10px;
    padding-bottom: 10px;
    .subname {
      text-align: center;
      padding: 15px;
      &:nth-child(2) {
        color: #000;
        padding: 5px 0;
      }
    }
    .cnt {
      font-size: 13px;
      color: #333333;
      span {
        color: #cc0000;
      }
    }
  }
}
</style>




